.profile__avatar-button {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    padding: 0;
    border: none;
}

.profile__avatar-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-image: url(../../../images/profile-avatar-edit.svg);
    background-color: rgba(0, 0, 0, 0.8);
    background-repeat: no-repeat;
    background-position: center;
}

.profile__avatar-button:hover::before {
    opacity: 1;
}